@use "../abstracts/index" as *;

.responses-ul {
	font-family: $main-font;

	.response-li {
		display: grid;
		grid-template-rows: 4rem 3.5rem fit-content fit-content;
		grid-template-columns: repeat(3, 1fr);

		margin: 7vh 1vw;

		.response-name {
			@include borderRadius();
			border: none;
			font-size: 1.1rem;
			background-color: $advizot-light;
			padding: 8px 12px;
			height: fit-content;
			width: fit-content;

			grid-row: 1;
			justify-self: center;
			align-self: center;

			&:hover {
				@include btnHover();
				cursor: default;
			}
		}
		.priority {
			grid-row: 1;
			grid-column: 2;
			justify-self: start;
			align-self: center;
		}
		.range-response {
			grid-row: 2 / 3;
			text-align: center;
		}
		.issue {
			grid-row: 3 / 4;
		}
		.goal {
			grid-row: 4 / 5;
		}
		.issue,
		.goal {
			grid-column: 1 / 4;
			margin-bottom: 0.5rem;

			h4 {
				text-align: center;
			}
		}

		.response-p {
			padding: 0.75rem 2vw 0.5rem;
			margin: 0 0.4rem;

			strong {
				text-decoration: underline;
			}
		}
	}
}
